import * as React from 'react';
import { ItemSlot, RenderToolbar, SlotType } from '@ali/4ever-component';
import type { Delimiter } from '@ali/4ever-utils';
import createMarkButton from './components/next/createMarkButton';

export default function factory(payload: {
  type: string,
  hotKey?: string,
  delimiter: Delimiter | Delimiter[],
  toolbarIcon: React.ReactElement,
  localeKeys?: {
    titleKey?: string;
    tooltipKey?: string;
  }
}) {
  const { type, hotKey, delimiter, toolbarIcon: defaultIcon, localeKeys} = payload;

  const Component = createMarkButton({ type, hotKey, defaultIcon, localeKeys, delimiter });

  return function createRenderToolbar(
    locale: Record<string, string>,
  ): RenderToolbar {

    return (props, controller) => {

      const { buttonName, extraProps } = props;

      if (buttonName === type) {
        return (
          <ItemSlot display={SlotType.iconButton}>
            <Component
              controller={controller}
              locale={locale}
              {...extraProps}
            />
          </ItemSlot>
        )
      }

      return null
    };
  }
}

